




































import {
  defineComponent, ref, PropType,
} from '@vue/composition-api';
import { acceptInvitation } from '@/api/email';

export interface Inviter {
  companyName?: string
  userName?: string
  groupAppName?: string
}

const IviteDialog = defineComponent({
  name: 'IviteDialog',
  dialogify: {
    props: {
      width: '600px',
      closeOnClickModal: false,
      center: true,
    },
  },
  components: {},
  props: {
    inviteId: {
      type: [String, Number],
      required: true,
    },
    inviter: {
      type: Object as PropType<Inviter>,
      required: true,
    },
  },

  setup(props, { root }) {
    const fromIvite = ref(true);
    async function sendInvitation(status: number) {
      // 关联的状态（1：接受，2：拒绝）
      const query = {
        inviteId: props.inviteId,
        status,
      };
      try {
        await acceptInvitation(query);
        root.$emit('refreshSpace');
      } catch (error) {
        root.$errHandler(error);
      }
    }
    function join() {
      sendInvitation(1);
    }

    function refuse() {
      sendInvitation(2);
    }

    return {
      fromIvite,
      join,
      refuse,
    };
  },
});
export default IviteDialog;
