










































import { defineComponent, PropType, computed } from '@vue/composition-api';
import { Item } from '../messageItem.vue';

const Type14Item = defineComponent({
  props: {
    item: {
      type: Object as PropType<Item>,
      required: true,
    },
  },
  setup(props) {
    const firstParam = computed(() => props.item.params[0]);
    return {
      firstParam,
    };
  },
});

export default Type14Item;

