










































import {
  defineComponent, reactive, computed, onMounted, toRefs, ref,
} from '@vue/composition-api';
import i18n from 'i18next';
import { confirmBool } from '@/components/index';
import axios from '@/api/axios';
import remove from '_/remove';
import MessageContent from './components/messageContent.vue';
import LayoutFooter from './components/footer.vue';
import { Item } from './components/messageItem.vue';

const tabList = [
  {
    label: 'Workspace Message',
    apiType: 'workspace',
    i18nKey: 'notification.workspace_message',
  },
  {
    label: 'System Message',
    apiType: 'system',
    i18nKey: 'notification.system_message',
  },
  {
    label: 'Recycle Bin',
    apiType: 'recycle',
    i18nKey: 'notification.recycle_bin',
  },
];
const MessageCenter = defineComponent({
  components: {
    MessageContent,
    LayoutFooter,
  },
  setup(_, { emit }) {
    const state = reactive({
      messageList: [] as Item[],
      loading: false,

      tabList,
      currentTabIndex: undefined as number | undefined,

      workspaceUnread: 0,
      systemUnread: 0,

      current: 1, // 当前页数
      pages: 1, // 总页数，为保持和后端一致，采用此名字
      total: 0, // 总个数

      slideBarLeft: 0,
    });

    const sbWrapper = ref<HTMLDivElement>();


    const noMore = computed(() => state.current >= state.pages);
    const disabled = computed(() => state.loading || noMore.value);
    const activeTab = computed(() => (state.currentTabIndex ? state.tabList[state.currentTabIndex].label : null));

    // 根据标签，调对应的列表的接口并返回结果
    async function fetchMessageList(params?: any) {
      state.loading = true;
      params = {
        cursor: 1,
        limit: 20,
        ...params,
      };
      const url = `/msgcenter/message/list/${state.tabList[state.currentTabIndex!].apiType}`;
      const result = await axios({
        method: 'get',
        url,
        params,
      });
      state.loading = false;
      return result;
    }

    // 更新新的页数、当前页数总页数总条数等信息
    function updatePageInfo(otherParams: typeof state) {
      Object.assign(state, otherParams);
    }

    // 变更当前活跃标签
    async function changeActiveTab(index: number) {
      if (state.currentTabIndex === index) {
        return;
      }
      state.messageList = [];
      state.currentTabIndex = index;
      state.slideBarLeft = state.currentTabIndex * (sbWrapper.value?.clientWidth as number / 3);
      const { data } = await fetchMessageList();
      const { records, ...otherParams } = data;
      state.messageList = records;
      updatePageInfo(otherParams);
    }
    // 获取加载下一页
    async function loadMoreMessages() {
      const { data } = await fetchMessageList({ cursor: ++state.current });
      const { records, ...otherParams } = data;
      state.messageList.push(...records);
      updatePageInfo(otherParams);
    }

    // 获取未读个数
    async function getUnreadCount() {
      const rs = await axios({
        method: 'get',
        url: '/msgcenter/message/detail',
      });
      state.workspaceUnread = rs.data.unread;
      state.systemUnread = rs.data.system;
      emit('update-has-new-message', rs.data);
    }

    // 处理一条信息已读
    async function handleReadMessage(item: Item) {
      item.status = 1;
      await axios({
        method: 'post',
        url: '/msgcenter/message/read',
        data: { ids: [item.id] },
      });
      getUnreadCount();
    }

    // 处理所有消息已读
    async function handleReadAllMessage() {
      // 后端参数，1是系统消息，2是协同空间消息
      const functionType = state.currentTabIndex === 0 ? 2 : 1;
      if (state.currentTabIndex !== 2) {
        state.messageList.forEach(each => { each.status = 1; });
        await axios({
          method: 'post',
          url: '/msgcenter/message/read/all',
          data: { functionType },
        });
        getUnreadCount();
      }
    }

    // 删除一条消息
    function handleDeleteMessage(item: Item) {
      axios({
        method: 'post',
        url: state.currentTabIndex === 2 ? '/msgcenter/message/delete' : '/msgcenter/message/recycle',
        data: { ids: [item.id] },
      });
      remove(state.messageList, item);
      --state.total;
    }

    //  处理删除所有信息
    async function handleDeleteAllMessage() {
      const index = state.currentTabIndex;
      if (index === 2) {
        const result = await confirmBool(i18n.t('notification.sure_empty_bin'), i18n.t('notification.clear_message'), {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          type: 'warning',
        });
        if (!result) return;
        axios({
          method: 'post',
          url: '/msgcenter/message/recycle/clean',
        });
        getUnreadCount();
      } else {
        const result = await confirmBool(i18n.t('notification.sure_clear_msg'), i18n.t('notification.clear_message'), {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          type: 'warning',
        });
        if (!result) return;
        const functionType = index === 0 ? 2 : 1;
        axios({
          method: 'post',
          url: '/msgcenter/message/recycle/all',
          data: { functionType },
        });
      }
      state.messageList = [];
      state.total = 0;
    }

    onMounted(() => {
      changeActiveTab(0);
      getUnreadCount();
    });

    return {
      ...toRefs(state),
      noMore,
      disabled,
      changeActiveTab,
      loadMoreMessages,
      fetchMessageList,
      updatePageInfo,
      getUnreadCount,
      handleReadMessage,
      handleReadAllMessage,
      handleDeleteMessage,
      handleDeleteAllMessage,
      sbWrapper,
      activeTab,
    };
  },
});

export default MessageCenter;
