<template>
  <div>
    <div style="color:#636363;" v-html="focusOnAtU(item.params[0].content)">
    </div>
    <div class="handle bottom-bar" v-if="!host" @click.stop="$emit('handle','comment',item)">
      <span class="shoe-name">
        #{{ item.params[0].params[0].shoeName }}
      </span>
      <span class="font-color-primary">{{ $t('notification.view') }}</span>
    </div>
  </div>
</template>
<script>
import { getHost } from '@/utils/exec';

export default {
  props: {
    item: {},
  },
  data() {
    return {
      host: undefined,
    };
  },
  async created() {
    this.host = await getHost();
  },
  methods: {
    // 把  @此用户的用户名  改成  @you
    focusOnAtU() {
      // eslint-disable-next-line
      return this.item.params[0].content.replace(/@(\w+) /g, (_, account) => {
        return `<span v-html style="color:#333">${_}</span>`;
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.icon-angle-right {
  font-size: 14px;
}
.shoe-name {
  color: $color-font-dark;
  margin-right: 8px;
}
.bottom-bar {
  font-size: 13px;
}
.font-color-primary {
  color: $color-primary;
}
</style>
