























import i18n from 'i18next';
import { RawLocation } from 'vue-router';
import { defineComponent, PropType } from '@vue/composition-api';
import { reloadModelRendererUrl } from '@/api/catalogue';
import { openSingleton } from '@/utils/open-window';
import { getInviterInfo } from '@/api/email';
import messageType, { MessageType } from './message-type';
import IviteDialog from './iviteDialog.vue';

export interface Params {
  params?: Params[]
  workspaceId?: string
  workspaceName?: string
  userName?: string
  status?: number
  department?: string
  inviteId?: string
  groupAppId?: string
  operatorId?: string
  operatorName?: string
  at?: string
  catalogId?: string
  content?: string
  versionId?: string
  versionName?: string
  supplement?: string
  // 会议相关
  meetingName?: string
  currentMeetingName?: string
  newMeetingEndTime?: string
  newMeetingIntroduce?: string
  newMeetingName?: string
  newMeetingStartTime?: string
  newMeetingThemeImage?: string
  oldMeetingName?: string
  updateUserName?: string
  newMeetingLogo?: string
  meetingStartTime?: string
  meetingEndTime?: string
  fullMeetingTime?: string
  inviter?: string
}

export interface Item {
  functionType: number
  gmtCreateTime: string
  gmtUpdateTime: string
  groupAppId: string
  id: string
  isDelete: number
  operationType: MessageType
  params: Params[]
  relateId: string
  status: number
  userId: string
}

const MessageItem = defineComponent({
  name: 'MessageItem',
  components: {
    IviteDialog,
  },
  props: {
    item: {
      required: true,
      type: Object as PropType<Item>,
    },
    activeTab: String,
  },
  setup(props, { emit, root: { $router, $errHandler, $injectDialog } }) {
    // 将未读信息转为已读信息
    function maskAsReaded(noteObj: Item) {
      emit('read', noteObj);
    }
    async function handle(from: string, item: Item) {
      if (from === 'pay') {
        $router.push({
          name: 'Order',
        });
        emit('close-msg-box');
      } else if (from === 'handle') {
        const inviteId = item.params[0].inviteId as string;

        const { data } = await getInviterInfo(`${inviteId}`);

        $injectDialog({
          is: IviteDialog,
          props: {
            inviteId,
            inviter: data,
          },
        });
      } else if (from === 'comment') {
        const at = item.params[0].params![0];
        try {
          const { data } = await reloadModelRendererUrl({
            catalogId: at.catalogId, from: at.workspaceId,
          });
          await openSingleton(async (): Promise<RawLocation> => {
            return {
              name: 'ModelItem',
              params: {
                modelId: data,
              },
              query: {
                workspaceId: at.workspaceId || undefined,
                catalogId: at.catalogId,
                // @ts-ignore
                commentDialogOpen: true,
              },
            };
          }, true, 'ModelItem');
          emit('close-msg-box');
          maskAsReaded(item);
        } catch (e) {
          $errHandler(e);
        }
      } else if (from === 'workspace') {
        $router.push({
          name: 'FileList',
          query: {
            appType: '13',
            relateId: item.params[0].groupAppId,
          },
        });
      }
    }
    function getTitleByType(typeId: MessageType, item: Item) {
      const dict = {
        1: item.params[0].operatorName,
        2: i18n.t('notification.msg.invitation_result'),
        // 已废弃
        3: i18n.t('notification.msg.invitation_reminder', { name: item.params[0].workspaceName }),
        4: i18n.t('notification.msg.subs_exp'),
        5: i18n.t('notification.msg.subs_exp_reminder'),
        // 已废弃
        6: i18n.t('notification.msg.trial_exp'),
        // 已废弃
        7: i18n.t('notification.msg.trial_exp_reminder'),
        8: i18n.t('notification.msg.account_change'),
        9: i18n.t('notification.msg.account_connect'),
        // 已废弃
        10: item.params[0].workspaceName,
        11: i18n.t('notification.msg.material_modified'),
        12: '供应商申请通过',
        13: '供应商申请未通过',
        14: i18n.t('notification.msg.meeting_info_alteration'),
        15: i18n.t('notification.msg.meetup_start_soon'),
        16: i18n.t('notification.msg.meetup_notification'),
        69: item.params[0].supplement ? i18n.t('notification.msg.material_reorder') : i18n.t('notification.msg.material_reorder_reject'),
      };
      return dict[typeId];
    }
    return {
      maskAsReaded, handle, getTitleByType, messageType,
    };
  },
});
export default MessageItem;
