<template>
  <div>
    <div>
      <!-- Material
      <span style="font-weight:700;">
        {{ paramsInfo ? paramsInfo.materialName : '' }}
      </span> was modified by
      <span style="font-weight:700;">
        {{ paramsInfo ? paramsInfo.userName : '' }}.
      </span>
      Modified infomation includes: -->
      <i18n path="notification.msg_type.material_modified">
        <span place="material" style="font-weight:700;">
          {{ paramsInfo ? paramsInfo.materialName : '' }}</span>
        <span place="user" style="font-weight:700;">
          {{ paramsInfo ? paramsInfo.userName : '' }}</span>
      </i18n>
      <span v-if="paramsInfo">
        <span v-for="(item, index) of paramsInfo.materialInfo" :key="index">
          {{ item }}
          <span v-if="index+1 < paramsInfo.materialInfo.length">
            ,
          </span>
          <span v-else>.</span>
        </span>
      </span>

    </div>
  </div>

</template>
<script>
import { getHost } from '@/utils/exec';

export default {
  props: {
    item: {},
  },
  computed: {
    params() {
      if (!this.item) return null;
      return this.item.params;
    },
    paramsInfo() {
      if (!this.item) return null;
      const {
        userName, email, materialName, senderEmail, ...materialInfo
      } = this.item.params[0];
      return {
        userName,
        email,
        materialName,
        materialInfo: Object.keys(materialInfo).filter(e => materialInfo[e]),
      };
    },
  },
  data() {
    return {
      host: undefined,
    };
  },
  async created() {
    this.host = await getHost();
  },
};
</script>
